import { HttpClient } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { environment } from 'src/environments/environment';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-info-user',
  templateUrl: './info-user.component.html',
  styleUrls: ['./info-user.component.scss'],
})
export class InfoUserComponent implements OnInit {
  user: any = {}; // Lu thng tin ngi dng
  userFormGroup: FormGroup; // Form iu khin

  constructor(private http: HttpClient, private formBuilder: FormBuilder) {}

  ngOnInit(): void {
    // Khi to form trc  khng gp li truy cp form cha c nh ngha
    this.userFormGroup = this.formBuilder.group({
      FirstName: ['', Validators.required],
      LastName: ['', Validators.required],
      SDT: ['', Validators.required],
      DiaChi: ['', Validators.required],
    });

    // Gi API ly thng tin ngi dng
    this.http.get(environment.URL_API + 'Auth/AuthHistory').subscribe(
      (res) => {
        this.user = res; // Gn d liu vo user
        this.getFullName();
        this.initializeForm(); // Khi to form vi gi tr t user
      },
      (error) => {
        console.error('Error fetching user data:', error);
      }
    );
  }

  // To fullName t h v tn
  getFullName(): void {
    this.user.fullName = this.user.firstName + ' ' + this.user.lastName;
  }

  // Khi to FormGroup vi d liu t user
  initializeForm(): void {
    // Cp nht gi tr ca form sau khi nhn d liu t API
    this.userFormGroup.patchValue({
      FirstName: this.user.firstName,
      LastName: this.user.lastName,
      SDT: this.user.sdt,
      DiaChi: this.user.diaChi,
    });
  }

  // Hm lu thng tin ngi dng
  Save(): void {
    // Chun b d liu gi i
    const updateData = {
      data: {
        firstName: this.userFormGroup.value.FirstName,
        lastName: this.userFormGroup.value.LastName,
        sdt: this.userFormGroup.value.SDT,
        diaChi: this.userFormGroup.value.DiaChi,
      },
    };

    // Gi d liu cp nht qua API
    this.http.post(environment.URL_API + 'Auth/AuthHistory', updateData).subscribe({
      next: (resp) => {
        Swal.fire('Cp nht thnh cng', '', 'success').then(() => {
          this.user = { ...this.user, ...updateData.data }; 
          console.log("user",this.user)
          this.getFullName();// Cp nht d liu hin th
        });
      },
      error: (err) => {
        Swal.fire('Li cp nht', err.message, 'error');
      },
    });
  }
}
